@charset "utf-8";
.navTop{
    height: 80px;
    background-color: #151c31;
    /*box-shadow: 0 1px 2px #999;*/
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.navTop ul{
    display: flex;
    color: #57697e;
}
.navTop ul li{
    width: 60px;
    height: 26px;
    line-height: 26px;
    text-align: center;
    border: 1px solid rgba(255, 255, 255, .3);
    margin-right: 16px;
}
.navTop ul li a{
    font-size: 14px;
    color: #57697e;
}

.navTop ul li a.active{
    color: orangered;
}


















