@charset "utf-8";
*{
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
ul{
    list-style-type: none;
}
#mian{
    display: flex;
    justify-content: space-between;
}
.side{
    width: 240px;
    height: 100vh;
    background: #2d2d36;
}
.con{
    width: calc(100% - 240px);
    height: 100vh;
}
.navTop{
    height: 60px;
    background: #151c31;
}
.logo{
    background: #37485d;
    height: 60px;
    overflow: hidden;
}
.logo img{
    display: block;
}

.navCon{
    height: calc(100vh - 60px);
    overflow: hidden;
    background: #000;
}
.navCon > div{
    height: 100%;
}




















@charset "utf-8";

.navleft ul{
    background: url(data:image/jpeg;base64,) no-repeat 20px 0;
}

.navleft ul li{
    height: 52px;
    line-height: 52px;
    text-indent: 4em;
    white-space: nowrap; /*文本不换行*/
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis; /*显示省略符号来代表被修剪的文本。*/
    margin-bottom: 6px;
}

.navleft ul li a {
    text-decoration: none;
    color: #57697e;
}
.navleft ul li a.active{
    color: orangered;
}


@charset "utf-8";
.navTop{
    height: 80px;
    background-color: #151c31;
    /*box-shadow: 0 1px 2px #999;*/
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.navTop ul{
    display: flex;
    color: #57697e;
}
.navTop ul li{
    width: 60px;
    height: 26px;
    line-height: 26px;
    text-align: center;
    border: 1px solid rgba(255, 255, 255, .3);
    margin-right: 16px;
}
.navTop ul li a{
    font-size: 14px;
    color: #57697e;
}

.navTop ul li a.active{
    color: orangered;
}




















