@charset "utf-8";
*{
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
ul{
    list-style-type: none;
}
#mian{
    display: flex;
    justify-content: space-between;
}
.side{
    width: 240px;
    height: 100vh;
    background: #2d2d36;
}
.con{
    width: calc(100% - 240px);
    height: 100vh;
}
.navTop{
    height: 60px;
    background: #151c31;
}
.logo{
    background: #37485d;
    height: 60px;
    overflow: hidden;
}
.logo img{
    display: block;
}

.navCon{
    height: calc(100vh - 60px);
    overflow: hidden;
    background: #000;
}
.navCon > div{
    height: 100%;
}



















