@charset "utf-8";

.navleft ul{
    background: url("./icon.jpg") no-repeat 20px 0;
}

.navleft ul li{
    height: 52px;
    line-height: 52px;
    text-indent: 4em;
    white-space: nowrap; /*文本不换行*/
    overflow: hidden;
    -o-text-overflow: ellipsis;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis; /*显示省略符号来代表被修剪的文本。*/
    margin-bottom: 6px;
}

.navleft ul li a {
    text-decoration: none;
    color: #57697e;
}
.navleft ul li a.active{
    color: orangered;
}

